.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
    max-width: var(--max-width);
    margin: 0 auto;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 1rem;
    background: linear-gradient( 45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 1rem;
    overflow: hidden;
    transform: rotate(10deg);
    height: 53vh;
}

.about__me-image:hover {
    transform: rotate(0);
    transition: var(--transition);

}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background:var(--color-bg-variant);
    border-radius: 1rem;
    border: 1px solid transparent;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}


.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;  
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);

}

@media screen and (max-width: 1024px) {

    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
       

    }
    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content {
        margin: 1rem 0 1.5rem;
    }

}


/* ======================== SMALL QUERIES (SMALL DEVICES)===================== */

@media screen and (max-width: 700px) {
    .about__me {
        width: 85%;
        margin: 0 auto 3rem;
    }   

    .about__cards {
        grid-template-columns: 1fr;
        gap: 1rem;
        margin: 1.5rem;
    }

    .about__content {
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }
}

